import React, { useState } from 'react';
import Taro, { useDidShow } from '@tarojs/taro';
import LectureList from '@/components/lecture-list/lecture-list';
import { ApiStore } from '@/store';
import PlayerFloat from '@/components/player-float/player-float';
import { track } from '@/utils/track';

const ChannelPlan = () => {
  useDidShow(() => {
    track.setEvent('lh_plan_show');
  });

  return (
    <>
      <LectureList from="plan" />
      {/* 播放悬窗 */}
      <PlayerFloat></PlayerFloat>
    </>
  );
};

export default ChannelPlan;
